import { authAPI } from './api'
import { storage } from '@utils/helpers'
import { VALIDATION } from '@utils/constants'

/**
 * Servicio de autenticacin para OSINTArgy
 * Maneja todas las operaciones relacionadas con autenticacin de usuarios
 */
export const authService = {
  /**
   * Iniciar sesin
   * @param {string} email - Email del usuario
   * @param {string} password - Contrasea del usuario
   * @returns {Promise<Object>} Resultado de la operacin
   */
  login: async (email, password) => {
    try {
      // Validar datos de entrada
      if (!email || !password) {
        return {
          success: false,
          message: 'Email y contrasea son requeridos'
        }
      }

      if (!VALIDATION.EMAIL_REGEX.test(email)) {
        return {
          success: false,
          message: 'Email no vlido'
        }
      }

      const response = await authAPI.login({ email, password })
      
      if (response.success) {
        const { user, token } = response.data
        
        // Guardar token en localStorage
        storage.set('auth_token', token)
        
        return {
          success: true,
          data: { user, token },
          message: 'Sesin iniciada correctamente'
        }
      }
      
      return {
        success: false,
        message: response.error || 'Error al iniciar sesin'
      }
    } catch (error) {
      console.error('Error en login:', error)
      return {
        success: false,
        message: 'Error de conexin. Intenta nuevamente.'
      }
    }
  },

  /**
   * Registrar nuevo usuario
   * @param {Object} userData - Datos del usuario
   * @returns {Promise<Object>} Resultado de la operacin
   */
  register: async (userData) => {
    try {
      const { name, email, password, confirmPassword } = userData

      // Validaciones
      if (!name || !email || !password || !confirmPassword) {
        return {
          success: false,
          message: 'Todos los campos son requeridos'
        }
      }

      if (!VALIDATION.EMAIL_REGEX.test(email)) {
        return {
          success: false,
          message: 'Email no vlido'
        }
      }

      if (password !== confirmPassword) {
        return {
          success: false,
          message: 'Las contraseas no coinciden'
        }
      }

      if (password.length < VALIDATION.MIN_PASSWORD_LENGTH) {
        return {
          success: false,
          message: `La contrasea debe tener al menos ${VALIDATION.MIN_PASSWORD_LENGTH} caracteres`
        }
      }

      const response = await authAPI.register({
        name: name.trim(),
        email: email.trim().toLowerCase(),
        password
      })
      
      if (response.success) {
        const { user, token } = response.data
        
        // Guardar token en localStorage
        storage.set('auth_token', token)
        
        return {
          success: true,
          data: { user, token },
          message: 'Cuenta creada correctamente'
        }
      }
      
      return {
        success: false,
        message: response.error || 'Error al crear la cuenta'
      }
    } catch (error) {
      console.error('Error en register:', error)
      return {
        success: false,
        message: 'Error de conexin. Intenta nuevamente.'
      }
    }
  },

  /**
   * Cerrar sesin
   * @returns {Promise<Object>} Resultado de la operacin
   */
  logout: async () => {
    try {
      // Llamar al endpoint de logout (opcional)
      await authAPI.logout()
      
      // Limpiar token local
      storage.remove('auth_token')
      
      return {
        success: true,
        message: 'Sesin cerrada correctamente'
      }
    } catch (error) {
      console.error('Error en logout:', error)
      
      // Aunque falle el logout en el servidor, limpiar token local
      storage.remove('auth_token')
      
      return {
        success: true,
        message: 'Sesin cerrada correctamente'
      }
    }
  },

  /**
   * Verificar token actual
   * @param {string} token - Token a verificar (opcional)
   * @returns {Promise<Object>} Datos del usuario si el token es vlido
   */
  verifyToken: async (token = null) => {
    try {
      const tokenToVerify = token || storage.get('auth_token')
      
      if (!tokenToVerify) {
        return null
      }

      const response = await authAPI.verifyToken()
      
      if (response.success) {
        return response.data.user
      }
      
      // Token invlido, limpiar storage
      storage.remove('auth_token')
      return null
    } catch (error) {
      console.error('Error al verificar token:', error)
      storage.remove('auth_token')
      return null
    }
  },

  /**
   * Refrescar token
   * @returns {Promise<Object>} Nuevo token
   */
  refreshToken: async () => {
    try {
      const response = await authAPI.refreshToken()
      
      if (response.success) {
        const { token } = response.data
        storage.set('auth_token', token)
        
        return {
          success: true,
          data: { token },
          message: 'Token actualizado'
        }
      }
      
      return {
        success: false,
        message: 'Error al actualizar token'
      }
    } catch (error) {
      console.error('Error al refrescar token:', error)
      return {
        success: false,
        message: 'Error al actualizar token'
      }
    }
  },

  /**
   * Obtener perfil del usuario
   * @returns {Promise<Object>} Datos del perfil
   */
  getProfile: async () => {
    try {
      const response = await authAPI.getProfile()
      
      if (response.success) {
        return {
          success: true,
          data: response.data,
          message: 'Perfil obtenido correctamente'
        }
      }
      
      return {
        success: false,
        message: response.error || 'Error al obtener perfil'
      }
    } catch (error) {
      console.error('Error al obtener perfil:', error)
      return {
        success: false,
        message: 'Error de conexin'
      }
    }
  },

  /**
   * Actualizar perfil del usuario
   * @param {Object} profileData - Datos del perfil a actualizar
   * @returns {Promise<Object>} Resultado de la operacin
   */
  updateProfile: async (profileData) => {
    try {
      const { name, email, bio, avatar } = profileData

      // Validaciones bsicas
      if (name && name.trim().length < 2) {
        return {
          success: false,
          message: 'El nombre debe tener al menos 2 caracteres'
        }
      }

      if (email && !VALIDATION.EMAIL_REGEX.test(email)) {
        return {
          success: false,
          message: 'Email no vlido'
        }
      }

      if (bio && bio.length > 500) {
        return {
          success: false,
          message: 'La biografa no puede exceder 500 caracteres'
        }
      }

      const dataToUpdate = {}
      if (name) dataToUpdate.name = name.trim()
      if (email) dataToUpdate.email = email.trim().toLowerCase()
      if (bio !== undefined) dataToUpdate.bio = bio.trim()
      if (avatar) dataToUpdate.avatar = avatar

      const response = await authAPI.updateProfile(dataToUpdate)
      
      if (response.success) {
        return {
          success: true,
          data: response.data,
          message: 'Perfil actualizado correctamente'
        }
      }
      
      return {
        success: false,
        message: response.error || 'Error al actualizar perfil'
      }
    } catch (error) {
      console.error('Error al actualizar perfil:', error)
      return {
        success: false,
        message: 'Error de conexin'
      }
    }
  },

  /**
   * Cambiar contrasea
   * @param {string} currentPassword - Contrasea actual
   * @param {string} newPassword - Nueva contrasea
   * @returns {Promise<Object>} Resultado de la operacin
   */
  changePassword: async (currentPassword, newPassword) => {
    try {
      // Validaciones
      if (!currentPassword || !newPassword) {
        return {
          success: false,
          message: 'Contrasea actual y nueva contrasea son requeridas'
        }
      }

      if (newPassword.length < VALIDATION.MIN_PASSWORD_LENGTH) {
        return {
          success: false,
          message: `La nueva contrasea debe tener al menos ${VALIDATION.MIN_PASSWORD_LENGTH} caracteres`
        }
      }

      if (currentPassword === newPassword) {
        return {
          success: false,
          message: 'La nueva contrasea debe ser diferente a la actual'
        }
      }

      const response = await authAPI.post('/auth/change-password', {
        currentPassword,
        newPassword
      })
      
      if (response.success) {
        return {
          success: true,
          message: 'Contrasea actualizada correctamente'
        }
      }
      
      return {
        success: false,
        message: response.error || 'Error al cambiar contrasea'
      }
    } catch (error) {
      console.error('Error al cambiar contrasea:', error)
      return {
        success: false,
        message: 'Error de conexin'
      }
    }
  },

  /**
   * Solicitar recuperacin de contrasea
   * @param {string} email - Email del usuario
   * @returns {Promise<Object>} Resultado de la operacin
   */
  forgotPassword: async (email) => {
    try {
      if (!email) {
        return {
          success: false,
          message: 'Email es requerido'
        }
      }

      if (!VALIDATION.EMAIL_REGEX.test(email)) {
        return {
          success: false,
          message: 'Email no vlido'
        }
      }

      const response = await authAPI.forgotPassword(email.trim().toLowerCase())
      
      if (response.success) {
        return {
          success: true,
          message: 'Se ha enviado un email con instrucciones para recuperar tu contrasea'
        }
      }
      
      return {
        success: false,
        message: response.error || 'Error al enviar email de recuperacin'
      }
    } catch (error) {
      console.error('Error en forgot password:', error)
      return {
        success: false,
        message: 'Error de conexin'
      }
    }
  },

  /**
   * Restablecer contrasea con token
   * @param {string} token - Token de recuperacin
   * @param {string} newPassword - Nueva contrasea
   * @returns {Promise<Object>} Resultado de la operacin
   */
  resetPassword: async (token, newPassword) => {
    try {
      if (!token || !newPassword) {
        return {
          success: false,
          message: 'Token y nueva contrasea son requeridos'
        }
      }

      if (newPassword.length < VALIDATION.MIN_PASSWORD_LENGTH) {
        return {
          success: false,
          message: `La contrasea debe tener al menos ${VALIDATION.MIN_PASSWORD_LENGTH} caracteres`
        }
      }

      const response = await authAPI.resetPassword(token, newPassword)
      
      if (response.success) {
        return {
          success: true,
          message: 'Contrasea restablecida correctamente'
        }
      }
      
      return {
        success: false,
        message: response.error || 'Error al restablecer contrasea'
      }
    } catch (error) {
      console.error('Error en reset password:', error)
      return {
        success: false,
        message: 'Error de conexin'
      }
    }
  },

  /**
   * Verificar si el usuario est autenticado
   * @returns {boolean} True si est autenticado
   */
  isAuthenticated: () => {
    const token = storage.get('auth_token')
    return !!token
  },

  /**
   * Obtener token actual
   * @returns {string|null} Token actual o null
   */
  getToken: () => {
    return storage.get('auth_token')
  },

  /**
   * Limpiar datos de autenticacin
   */
  clearAuth: () => {
    storage.remove('auth_token')
  }
}

export default authService